.link{border:2px solid #639;color:#639;display:inline-block;padding:.75rem 1rem;position:relative;text-decoration:none}.link:after{background-color:#639;bottom:0;content:"";height:100%;left:0;margin-top:0;position:absolute;right:0;top:0;-webkit-transform:scaleY(0);transform:scaleY(0);-webkit-transform-origin:bottom;transform-origin:bottom;transition:.25s ease-in-out;width:100%;z-index:-1}.link:hover{color:#fff}.link:hover:after{-webkit-transform:scaleY(1);transform:scaleY(1)}